@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200&display=swap);
footer{
  position:relative;
  
  display:block;  
  width:100%;
  background-color: azure;

  
}


@media only screen and (max-width: 768px){
  footer{
    background-color: azure;
    position:relative;
    bottom:0;
    display:block;  
    width:100%
    
  }
}
.buttonContainer {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
  }

  .homepage{
    min-height: 93vh;
    display: flex;
    align-items: center;
    background-image: url("https://st4.depositphotos.com/1106005/39218/i/450/depositphotos_392181926-stock-photo-sticky-note-background-multi-color.jpg");
    background-position: center;
    background-size: cover;
  }

  .landingbutton {
    width: 200px;
    height: 55px;
  }

  .title {
    font-size: 55px;
  }
  .subtitle {
    font-size: 25px;
    padding-top: 10px;
  }

  .intro-text {
    width: 100%;
    text-align: center;
  }


  @media only screen and (min-width: 1200px) {
    .buttonContainer {
      padding: 0 200px;
    }
    .title {
      padding: 0 100px;
    }
  }
  
  @media only screen and (max-width: 768px) {
    .title {
      font-size: 60px;
    }
    .landingbutton {
      width: 150px;
      height: 50px;
    }

    .subtitle {
        font-size: 25px;
        padding-top: 0px;
      }
  }
.mainscreen{
    min-height: 93vh;
    display: flex;
    padding: 10px 0;
}

.page {
    width: 100%;
  }

  .heading {
    font-size: 60px;
    /* font-family: "Ubuntu", sans-serif; */
    font-family: "Work Sans", sans-serif;
    padding: 5px 10px;
  }
  
  @media only screen and (min-width: 1200px) {
  }
  
  @media only screen and (max-width: 600px) {
    .heading {
      font-size: 30px;
      display: flex;
      justify-content: center;
    }
  }
.loginContainer {
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
